@font-face {
  font-family: "bluu";
  src: url("../assets/fonts/bluunext-bold-webfont.woff2") format("woff2"), url("../assets/fonts/bluunext-bold-webfont.woff") format("woff"), url("../assets/fonts/bluunext-bold.ttf") format("truetype"), url("../assets/fonts/bluunext-bold-webfont.svg#bluu_nextbold") format("svg");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "bluu";
  src: url("assets/fonts/bluunext-bolditalic-webfont.woff2") format("woff2"), url("assets/fonts/bluunext-bolditalic-webfont.woff") format("woff"), url("assets/fonts/bluunext-bolditalic.ttf") format("truetype"), url("assets/fonts/bluunext-bolditalic-webfont.svg#bluu_nextbold_italic") format("svg");
  font-weight: normal;
  font-style: italic;
}
@font-face {
  font-family: "bluu_nexttitling";
  src: url("assets/fonts/BluuNext-Titling.ttf") format("truetype"), url("assets/fonts/bluunext-titling.woff2") format("woff2"), url("assets/fonts/bluunext-titling.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  font-family: "bluu", sans-serif;
}

div {
  display: block;
}

:root {
  --color-blue: #0285ee;
  --color-lavender: #887dff;
  --color-brighterpurple: #590042;
  --color-brightpurple: #380051;
  --color-purple: #4801ff;
  --color-violet: #2d003d;
  --color-darkviolet: #24001c;
  --color-lightyellow: #f1e002;
  --color-yellow: #c39c0b;
  --color-gold: #d09601;
  --color-brown: #432102;
  --color-browner: #491900;
  --color-redbrown: #2b0700;
  --color-yellowbrown: #272401;
  --color-darkredbrown: #1c0e00;
  --color-red: #5c0014;
  --color-darkred: #2c0013;
  --color-darkestred: #1a0005;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

body {
  font-size: 16px;
  background: var(--color-purple);
  transition: 1s ease-in 0.5s;
  line-height: 1.5;
  word-wrap: break-word;
  font-weight: 500;
}

.top-matter {
  display: flex;
  width: 90vw;
  margin: 3em;
  margin-right: auto;
  margin-left: auto;
  flex-direction: row;
}
@media (max-width: 1000px) {
  .top-matter {
    max-width: none;
    margin-left: 1em;
    flex-direction: column;
  }
}

.title {
  font-size: 70px;
  line-height: 72px;
}

.week {
  font-size: 70px;
  line-height: 72px;
  text-align: right;
}
@media (max-width: 1000px) {
  .week {
    max-width: none;
    flex-direction: column;
  }
}

.transition-mod-title {
  transition: transform 3s;
  transform: none;
}

.transition-in {
  transition: 0.25s;
}

.transition-clicked {
  transition: 2s ease-in 0.5s;
}